import UserHeader from '../components/UserHeader.vue'
export default {
  component: {
    UserHeader,
  },
  data() {
    return {
      title: '',

      formData: {
        title: this.title,
      },
    }
  },
  methods: {
    async CreatePost() {
      await this.$store.dispatch('CreatePost', this.formData)
      this.$router.push('/UserHome')
    },
  },
  head() {
    return {
      title: 'Create Tweet Page',
    }
  },
}
